<template>
  <div id="home">
    <h1>{{currentText}}</h1>
    <h2>{{timetext}}</h2>
    <div id="elcom">
      <NEllipse></NEllipse>
    </div>

    <b-row>
      <b-col>
        <b-btn variant="light"  v-if="status !==1" @click="start">
          <font-awesome-icon :icon="['fas','play']"></font-awesome-icon>
        </b-btn>
        <b-btn  variant="light"  v-if="status==1" @click="pause">
          <font-awesome-icon :icon="['fas','pause']"></font-awesome-icon>
        </b-btn>
      </b-col>
      <b-col>
        <b-btn variant="secondary" v-if="current.length > 0 || todos.legth > 0" @click="finish(true)">
          <font-awesome-icon :icon="['fas','step-forward']"></font-awesome-icon>
        </b-btn>
        <b-btn variant="secondary" v-else disabled>
          <font-awesome-icon :icon="['fas','step-forward']"></font-awesome-icon>
        </b-btn>
      </b-col>
    </b-row>

  </div>
</template>

<script>
import NEllipse from '../components/NEllipse.vue'
export default {
  name: 'home',
  data () {
    return {
      // 0 = 停止
      // 1 = 播放
      // 2 = 暫停
      status: 0,
      timer: 0
    }
  },
  computed: {
    currentText () {
      return this.current.length > 0
        ? this.current
        : this.todos.length > 0
          ? '點擊開始'
          : '沒有事項'
    },
    timetext () {
      const m = Math.floor(this.timeleft / 60)
      const s = Math.floor(this.timeleft % 60)
      return `${m}：${s}`
    },
    alarm () {
      return this.$store.getters.alarm
    },
    timeleft () {
      return this.$store.getters.timeleft
    },
    current () {
      return this.$store.getters.current
    },
    todos () {
      return this.$store.getters.todos
    }
  },
  methods: {
    start () {
      if (this.status === 2) {
        // 暫停後繼續
        this.status = 1
        this.timer = setInterval(() => {
          this.$store.commit('countDown')
          if (this.timeleft <= 0) {
            this.finish(false)
          }
        }, 1000)
      } else {
        // 開始全新的倒數
        if (this.todos.length > 0) {
          // 向store傳達開始全新倒數
          this.$store.commit('start')
          this.status = 1
          this.timer = setInterval(() => {
            this.$store.commit('countDown')
            if (this.timeleft <= 0) {
              this.finish(false)
            }
          }, 1000)
        }
      }
    },
    finish (skip) {
      clearInterval(this.timer)
      this.status = 0
      this.$store.commit('finish')

      if (!skip) {
        const audio = new Audio()
        audio.src = './alarms/' + this.alarm
        audio.play()
      }

      if (this.todos.length > 0) {
        this.start()
      } else {
        setTimeout(() => {
          alert('時間到，事情都完成了嗎？')
          // Swal.fire(
          //   'Good job!',
          //   'You clicked the button!',
          //   'success'
          // )
        }, 10)
      }
    },
    // 暫停
    pause () {
      clearInterval(this.timer)
      this.status = 2
    }
  },
  components: {
    NEllipse
  }
}
</script>
